.body, html {
    height: 100vh;
    position: relative;
    background-image: url("./images/cloth.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.nav {
    padding: 20px 0;
    display: flex;
    justify-content: center;
}

.btn {
    border-radius: 3px;
    background-color: white;
    color: black;
    border: 2px solid #e7e7e7;
    padding: 15px 32px;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    margin: 0 10px;
}

.card-deck__card {
    transform: translate(-50%, -50%);
    width: 135px;
    height: 185px;
    background-image: url("./images/images.png");
    position: absolute;
    cursor: pointer;
    z-index: 156;
    top: 300px;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: inset 0 0 6px #000000;
    border-radius: 10px;
}

.players-container {
    height: 100vh;
}

.player {
    position: relative;
    height: 50%;
}

.player__name {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    text-transform: uppercase;
    color: white;
}

.player__name-turn {
    color: rgba(0,0,0, .9);
    margin-left: 10px;
}

.card-bank .card-deck__card {
    background-image: none;
    background-color: white;
}

.card-deck__card__card-title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: auto;
    font-size: 70px;
}

.win-title {
    text-align: center;
    font-size: 40px;
    color: white;
}